export default {
    isAuthenticated: state => {
        return state.email != null && state.email !== '';
    },

    getUserId: state => {
        return state.userId;
    },

    getEmail: state => {
        return state.email;
    },

    getToken: state => {
        return state.token;
    },
    getRole:state=>{
        return state.userRole
    }
}
